// ===========================|| PROFILE 1 - PROFILE ACCOUNT ||=========================== //

const PersonalAccount = () => {

    const [currency, setCurrency] = React.useState('One');
    const [business, setBusiness] = React.useState('None');

      const handleChange = (event) => {
        setCurrency(event.target.value);
      };

      const handleChange_business = (event) => {
        setBusiness(event.target.value);
      };






    const dispatch = useDispatch();

    const {user, url} = useContext(JwtAuthContext)
    const [store, setStore] = useState([])
    const [isLoading, setIsLoading] = useState(false)
    const [bool, setBool] = useState(true)
    const [category, setCategory] = React.useState('Cat1');
    const handleChange1 = (event) => {
        setCategory(event.target.value);
    };

    const [goal, setGoal] = React.useState('SDG_One');
    const handleChange2 = (event) => {
        setGoal(event.target.value);
    };

    useEffect( async () => {
        var requestOptions = {
          method: 'GET',
          redirect: 'follow'
        };

        const res = await fetch(`${url}/api/single-store/${user.id}`, requestOptions)
        const data = await res.json()
        console.log(data)
        setStore(data)
    }, [bool])

    const onSubmit = async (e) => {
         e.preventDefault()

        if (e.target.name.value === "") {
            e.target.name.value = store.name
        } 

        if(e.target.currency.value === "") {
            e.target.currency.value = store.sdg_goals
        } 

        if(e.target.desc.value === "") {
            e.target.desc.value = store.desc
        } 

        if(e.target.category.value === "") {
            e.target.category.value = store.category
        } 

        /* if(e.target.store_logo.value === "") {
            e.target.store_logo.value = store.store_logo
        } */ 

        console.log(e.target.currency.value)

        var formdata = new FormData();
        formdata.append("name", e.target.name.value);
        formdata.append("desc", e.target.desc.value);
        formdata.append("category", e.target.category.value);
        formdata.append("sdg_goals", e.target.currency.value);
        formdata.append("business_type", e.target.business.value);



        var requestOptions = {
          method: 'PATCH',
          body: formdata,
          redirect: 'follow'
        };


        const res = await fetch(`${url}/api/patch-store/${user.id}/`, requestOptions)
          console.log(res)

         if (res.status === 200) {
            setIsLoading(false)
             dispatch({
            type: SNACKBAR_OPEN,
            open: true,
            message: 'Store Updated',
            variant: 'alert',
            alertSeverity: 'success'
        });
        } else {
            setIsLoading(false)
             dispatch({
            type: SNACKBAR_OPEN,
            open: true,
            message: 'Failed',
            variant: 'alert',
            alertSeverity: 'error'
        });
    }
}


const Submit = async (e) => {
         e.preventDefault()

        const upload_file = e.target.store_logo.files[0];

        var formdata = new FormData();
        formdata.append("store_logo", upload_file); 


        var requestOptions = {
          method: 'PATCH',
          body: formdata,
          redirect: 'follow'
        };


        const res = await fetch(`${url}/api/patch-store-logo/${user.id}/`, requestOptions)
          console.log(res)

         if (res.status === 200) {
            setBool(!bool)
            setIsLoading(false)
             dispatch({
            type: SNACKBAR_OPEN,
            open: true,
            message: 'Store Logo Updated',
            variant: 'alert',
            alertSeverity: 'success'
        });
        } else {
            setIsLoading(false)
             dispatch({
            type: SNACKBAR_OPEN,
            open: true,
            message: 'Failed',
            variant: 'alert',
            alertSeverity: 'error'
        });


}
}

 let myImage

        try {
            myImage = `${url}${store.store_logo}`
            
        }catch (error) {
            console.log(myImage)
            myImage = ''
        }





    return (
        <Grid container spacing={gridSpacing}>
            <Grid item xs={12} md={6}>
                    <form  onSubmit={onSubmit} action="#" noValidate autoComplete="off">
                        <Grid container spacing={gridSpacing}>
                            <Grid item xs={12} >
                                <TextField 
                                    id="outlined-basic1" 
                                    fullWidth label="Store Name" 
                                    name="name"
                                    type="text"
                                    defaultValue={store.name} />
                            </Grid>
                            <Grid item xs={12}>
                                <TextField
                                    /* id="outlined-select-currency" */
                                    id="outlined-basic1"
                                    fullWidth
                                    label="Category"
                                    /* value={category} */
                                    name="category"
                                    type="text"
                                    defaultValue={store.category}
                                    /* onChange={handleChange1} */
                                >
                                    {/* {categories.map((option) => (
                                        <MenuItem key={option.value} value={option.value}>
                                            {option.label}
                                        </MenuItem>
                                    ))} */}
                                </TextField>
                            </Grid>
                            <Grid item xs={12}>
                                <TextField
                                    id="outlined-multiline-static1"
                                    label="Description"
                                    name="desc"
                                    type="text"
                                    multiline
                                    fullWidth
                                    rows={3}
                                    defaultValue={store.desc}
                                />
                            </Grid>
                            {/* <Grid item xs={12}>
                                <TextField
                                    id="outlined-select-experience"
                                    id="outlined-basic1"
                                    fullWidth
                                    label="SDG Goal"
                                    name="sdg_goals"
                                    type="text"
                                    defaultValue={store.sdg_goals}
                                    value={goal}
                                    onChange={handleChange2}
                                >
                                    {goals.map((option) => (
                                        <MenuItem key={option.value} value={option.value}>
                                            {option.label}
                                        </MenuItem>
                                    ))}
                                </TextField>
                            </Grid> */}

                            <Grid item xs={12}>
                                <TextField
                                  id="outlined-basic1"
                                  select
                                  fullWidth
                                  label="Select"
                                  name="currency"
                                  value={currency}
                                  onChange={handleChange}
                                  helperText="Please select your GOAL"
                                  
                                >
                                  {currencies.map((option) => (
                                    <MenuItem key={option.value} value={option.value}>
                                      {option.label}
                                    </MenuItem>
                                  ))}
                                </TextField>

                                
                            </Grid>

                            <Grid item xs={12}>
                                <TextField
                                  id="outlined-basic2"
                                  select
                                  fullWidth
                                  label="Select"
                                  name="business"
                                  value={business}
                                  onChange={handleChange_business}
                                  helperText="Please select your Business Type"
                         
                                >
                                  {businesses.map((option) => (
                                    <MenuItem key={option.value} value={option.value}>
                                      {option.label}
                                    </MenuItem>
                                  ))}
                                </TextField>

                                
                            </Grid>
                        
                            {/* <Grid item xs={12}>
                                <input
                                    
                                    id="outlined-multiline-static2"
                                    label="Store Logo"
                                    name="store_logo"
                                    type="file"
                                    fullWidth
                                    rows={3}
                                    defaultValue=""
                                />
                               
                            </Grid> */}
                        
                        <Grid item>
                                <AnimateButton>
                                    <Button type="submit" variant="contained" size="medium" color="secondary">
                                        {isLoading ? <img src={loader} alt="" style={{width:'25px', height:'25px', marginLeft:'8px'}}/>:'UPDATE STORE'}
                                    </Button>
                                </AnimateButton>
                        </Grid>
                        </Grid>
                    </form>

            </Grid>
            <Grid item xs={12} md={6}>
                <SubCard title={store.name}>
                    <Grid container alignItems="center" spacing={gridSpacing} sx={{marginBottom: '12px'}} >
                        <img src={myImage} alt="" style={{height: '360px'}}/>
                    </Grid>
                    <form  onSubmit={Submit} action="#" noValidate autoComplete="off">
            
                    <Grid item xs={12}>
                                <input
                                    
                                    id="outlined-multiline-static2"
                                    label=" Change Store Logo"
                                    name="store_logo"
                                    type="file"
                                    fullWidth
                                    rows={3}
                                    /* defaultValue="" */
                                />
                               
                            </Grid>
                        
                        <Grid item>
                                <AnimateButton>
                                    <Button type="submit" variant="contained" size="small" color="secondary"  sx={{marginTop: '5px'}}>
                                        {isLoading ? <img src={loader} alt="" style={{width:'25px', height:'25px', marginLeft:'8px'}}/>:'UPDATE STORE LOGO'}
                                    </Button>
                                </AnimateButton>
                        </Grid>
           
                    </form>
                    {/* <form noValidate autoComplete="off">
                        <Grid container alignItems="center" spacing={gridSpacing} sx={{ mb: '10px' }}>
                            <Grid item>
                                <FacebookIcon />
                            </Grid>
                            <Grid item xs zeroMinWidth>
                                <TextField fullWidth label="Facebook Profile Url" />
                            </Grid>
                            <Grid item>
                                <AnimateButton>
                                    <Button variant="contained" size="small" color="secondary">
                                        Connect
                                    </Button>
                                </AnimateButton>
                            </Grid>
                        </Grid>
                        <Grid container alignItems="center" spacing={gridSpacing} sx={{ mb: '10px' }}>
                            <Grid item>
                                <TwitterIcon />
                            </Grid>
                            <Grid item xs zeroMinWidth>
                                <TextField fullWidth label="Twitter Profile Url" />
                            </Grid>
                            <Grid item>
                                <AnimateButton>
                                    <Button variant="contained" size="small" color="secondary">
                                        Connect
                                    </Button>
                                </AnimateButton>
                            </Grid>
                        </Grid>
                        <Grid container alignItems="center" spacing={gridSpacing}>
                            <Grid item>
                                <LinkedInIcon />
                            </Grid>
                            <Grid item xs zeroMinWidth>
                                <TextField fullWidth label="LinkedIn Profile Url" />
                            </Grid>
                            <Grid item>
                                <AnimateButton>
                                    <Button variant="contained" size="small" color="secondary">
                                        Connect
                                    </Button>
                                </AnimateButton>
                            </Grid>
                        </Grid>
                    </form>*/}
                </SubCard>
            </Grid> 
        </Grid>
    );
};

export default PersonalAccount;








